
.movie img{
    position : relative;
    top : -50px;
    max-width : 150px;
    width : 100%;
    margin-right : 30px;
}

.movie .movie__title,
.movie .movie__year{
    margin : 0;
}

.movie .movie__title{
    font-weight : 400;
    height : 30px;
    overflow: hidden;
    font-size : 22px;
    text-overflow: ellipsis;
    margin-bottom: 5px;
}

.movie .movie__year{
    font-size : 12px;
    margin-bottom: 10px;
}

.movie .movie__year,
.movie .movie__genres{
    color :#C4C5CB;
    font-weight: 300;
}

.movie .movie__genres{
    display: flex;
    padding : 0;
    margin : 0;
    list-style-type : none;
}
.movie .genres__genre{
   
    font-size : 15px;
    margin-right: 5px;
}

.movie .movie__summary{
    height : 105px;
    overflow: hidden;
    font-weight: 300;
    font-size : 12px;
    text-overflow: ellipsis;
}