@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700&display=swap);

.movie img{
    position : relative;
    top : -50px;
    max-width : 150px;
    width : 100%;
    margin-right : 30px;
}

.movie .movie__title,
.movie .movie__year{
    margin : 0;
}

.movie .movie__title{
    font-weight : 400;
    height : 30px;
    overflow: hidden;
    font-size : 22px;
    text-overflow: ellipsis;
    margin-bottom: 5px;
}

.movie .movie__year{
    font-size : 12px;
    margin-bottom: 10px;
}

.movie .movie__year,
.movie .movie__genres{
    color :#C4C5CB;
    font-weight: 300;
}

.movie .movie__genres{
    display: flex;
    padding : 0;
    margin : 0;
    list-style-type : none;
}
.movie .genres__genre{
   
    font-size : 15px;
    margin-right: 5px;
}

.movie .movie__summary{
    height : 105px;
    overflow: hidden;
    font-weight: 300;
    font-size : 12px;
    text-overflow: ellipsis;
}
*{
    box-sizing: border-box;
}

body{
    margin : 0;
    padding : 0;
    font-family: 'Open Sans', sans-serif;
    background-color : #eff3f7;
    height : 100%;
}

html, body, #potato, .container{
    height : 100%;
    display : flex;
    justify-content: center;
}

.loader{
    width : 100%;
    height : 100%;
    display : flex;
    justify-content: center;
    align-items : center;
}

.movies{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: wrap;
    padding : 50px;
    padding-top: 70px;
    width : 80%;
}

.movies .movie {
    height: 256px;
    display : flex;
    width : 45%;
    background-color: white;
    margin-bottom: 100px;
    align-items: flex-start;
    justify-content: flex-start; 
    padding : 25px;
    border-radius: 5px;
    box-shadow: 5px 5px 15px #D2D1DE;
}

