@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,700&display=swap');

*{
    box-sizing: border-box;
}

body{
    margin : 0;
    padding : 0;
    font-family: 'Open Sans', sans-serif;
    background-color : #eff3f7;
    height : 100%;
}

html, body, #potato, .container{
    height : 100%;
    display : flex;
    justify-content: center;
}

.loader{
    width : 100%;
    height : 100%;
    display : flex;
    justify-content: center;
    align-items : center;
}

.movies{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: wrap;
    padding : 50px;
    padding-top: 70px;
    width : 80%;
}

.movies .movie {
    height: 256px;
    display : flex;
    width : 45%;
    background-color: white;
    margin-bottom: 100px;
    align-items: flex-start;
    justify-content: flex-start; 
    padding : 25px;
    border-radius: 5px;
    box-shadow: 5px 5px 15px #D2D1DE;
}
